}

.portfolio_items-image {
    border-radius: 1.5rem;
    overflow: hidden;
}

.portfolio_items h3{
    margin: 1.2rem 0 2rem;
}

.portfolio_items-cta {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
    
}

@media screen and (max-width: 1024px) {
    .portfolio_container {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1.2rem;
        gap: 1.2rem;
    }

   
}

@media screen and (max-width: 600px) {
 
    .portfolio_container {
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
}
}
.services_container {
display: grid;
grid-template-columns: repeat(3, 1fr);
grid-gap: 3rem;
gap: 3rem;

