* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;

}

:root {
    --color-bg: #1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary: #ff4d4d;
    --color-primary-variant: rgba(77,181,255,0.4);
    --color-white: #fff;
    --color-light: rgba(255,255,255, 0.6);

    transition: all 400ms ease;

    --container-widght-lg: 75%;
    --container-widght-md: 86%;
    --container-width-ms: 90%;

}

html{ 
    scroll-behavior:  smooth;
}

::-webkit-scrollbar {
    display: none;

}

body {
    font-family: 'Poppins', sans-serif;
    background:  #1f1f38;
    background:  var(--color-bg);
    color: #fff;
    color: var(--color-white);
    line-height:  1.0;
    background-image: url(react-portfolio-website/static/media/fundoo.b3238cca.jpg);
    
}

/* =========== General Styles ============== */

.container {
    width: 75%;
    width: var(--container-widght-lg);
    margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5{
    font-weight: 590;
}


h1{
    font-size: 2.5rem;

}

section {
    margin-top: 8rem;
}

section > h2, section > h5 {

    text-align: center;
    color: rgba(255,255,255, 0.6);
    color: var(--color-light);
}

section >h2 {
    color: #ff4d4d;
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: rgba(255,255,255, 0.6);
    color: var(--color-light);
 
}

a {
    color: #ff4d4d;
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover {
    color: #fff;
    color: var(--color-white)
}

.btn {
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #ff4d4d;
    color: var(--color-primary);
    padding: .75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #ff4d4d;
    border: 1px solid var(--color-primary);
    transition: var(--transition);

}
.btn:hover {
    background: #fff;
    background: var(--color-white);
    color: #1f1f38;
    color: var(--color-bg);
    border-color: red;
}

.btn-primary {
    background: #ff4d4d;
    background: var(--color-primary);
    color: #1f1f38;
    color: var(--color-bg);
   
}

img {
    display:  block;
    width: 100%;
    object-fit: cover;
}


/* -------------------- Media queries ( Medium devices) ------- */

@media screen and (max-width: 1024px) {
    .container{
        width: 86%;
        width: var(--container-widght-md);
    }
}

section {
    margin-top: 6rem;

}
    

/* -------------------- Media queries ( Small devices) ------- */

@media screen and (max-width: 600px) {
    .container{
        width: 90%;
        width: var(--container-width-ms);
     }
}

section > h2 {
    margin-bottom: 2rem;

} 

.me {
    width: 400px;
    height: 100px;
    object-fit: scale-down;

}


